import ArrowRight from '@assets/arrow-right.svg?react';
import TextButton from '../Button/TextButton';
import IconButton from '../Button/IconButton';
import { motion } from 'framer-motion';
import Heading2 from '../Heading2';
import Marquee from "../MagicUI/MarqueeForClients";
import { useMediaQuery } from 'react-responsive';
import { useState, useEffect } from 'react';

const clientImages = Object.keys(
  import.meta.glob('@assets/clients/*.{png,PNG}', {
    query: 'url',
  }),
);

const Clients = () => {
  const isMobile = useMediaQuery({ maxWidth: 767 });

  const [selectedImages, setSelectedImages] = useState<string[]>([]);

  useEffect(() => {
    const shuffled = clientImages.sort(() => 0.5 - Math.random());
    setSelectedImages(shuffled.slice(0, 9));
  }, []);

  const clientRows = [];
  for (let i = 0; i < selectedImages.length; i += 3) {
    clientRows.push(selectedImages.slice(i, i + 3));
  }

  return (
    <div className="">
      <div className="mx-auto max-w-screen-xl px-[min(2.5rem,3vw)] py-[min(8rem,10vw)] xl:px-0">
        <Heading2 className="mb-[min(4rem,5vw)] text-[#290D05]">
          Our Clients
        </Heading2>
        {isMobile ? (
           clientRows.map((row, rowIndex) => (
            <Marquee className="[--duration:5s]" key={rowIndex} reverse={rowIndex % 2 === 1}>
              {row.map((image) => (
                <div
                  key={image}
                  className="flex items-center justify-center rounded-full bg-[#F6F4F2] p-[min(1rem,2vw)] transition-shadow"
                >
                  <img
                    className="h-[min(4rem,10vw)] object-contain p-1"
                    src={image}
                    alt={image}
                  />
                </div>
              ))}
            </Marquee>))
        ) : (
        <div className="grid grid-flow-row grid-cols-3 justify-center gap-[min(0.6rem,1.4vw)] md:grid-cols-6">
          {clientImages.map((image) => (
            <div
              className="flex items-center justify-center rounded-full bg-[#F6F4F2] p-[min(1rem,2vw)] transition-shadow"
              key={image}
            >
              <img
                className="h-[min(4rem,10vw)] object-contain p-1"
                src={image}
                alt={image}
              />
            </div>
          ))}
        </div>)}
        <motion.div
          whileHover={'animate'}
          className="mx-auto mt-[min(4rem,5vw)] flex w-fit justify-center"
        >
          <TextButton className="rounded-full bg-primary px-6 py-4 font-semibold text-white">
            Awards
          </TextButton>
          <IconButton>
            <ArrowRight />
          </IconButton>
        </motion.div>
      </div>
    </div>
  );
};

export default Clients;
