import { cn } from '../../lib/utils';

interface IHeading2 {
  children: string;
  className?: string;
}

const Heading2 = ({ children, className }: IHeading2) => {
  return (
    <h2
      className={cn(
        'text-[clamp(2.5rem,1.25vw+1.8rem,3.50rem)] font-bold',
        className,
      )}
    >
      {children}
    </h2>
  );
};
export default Heading2;
