import { motion } from 'framer-motion';
import TextButton from '../Button/TextButton';
import IconButton from '../Button/IconButton';
import ArrowRight from '@assets/arrow-right.svg?react';
import Slider from '../Slider/Slider';
import Heading2 from '../Heading2';

const FeaturedCampaigns = () => {
  return (
    <div className="px-[min(3vw,2.5rem)] py-[min(10vw,8rem)]">
      <div className="mx-auto mb-6 flex max-w-screen-xl flex-wrap items-start justify-between sm:gap-10">
        <Heading2 className="mb-6 sm:mb-16 text-[#290D05]">Feature Campaigns</Heading2>
        <motion.a
          whileHover={'animate'}
          className="group flex items-center text-white"
          href="/featured-campaigns"
        >
          <TextButton className="h-14 rounded-full bg-primary px-8 py-2 font-semibold">
            View all
          </TextButton>
          <IconButton>
            <ArrowRight />
          </IconButton>
        </motion.a>
      </div>
      <Slider />
    </div>
  );
};

export default FeaturedCampaigns;
