/* eslint-disable jsx-a11y/anchor-is-valid */
import Marquee from '../Marquee';
import ArrowTopRight from '@assets/arrow-top-right.svg?react';
import { MouseEvent } from 'react';

const columns = [
  {
    header: 'Contact Us',
    items: [
      {
        name: 'inquiry@ims.vn',
        type: 'email',
      },
      {
        name: '038 3824 8111',
        type: 'tel',
      },
      {
        name: 'Head office 1st Floor, 194 Nguyen Cong Tru St, Dist 1, HCM, Viet Nam',
        type: 'address',
      },
    ],
  },
  {
    header: 'Pages',
    items: [
      {
        name: 'About IMS',
        url: '#about',
      },

      {
        name: 'Featured campaigns',
        url: '/featured-campaigns',
      },
      {
        name: 'Our news',
        url: '/our-news',
      },
    ],
  },
  {
    header: 'Links',
    items: [
      {
        name: 'Facebook',
        url: 'https://www.facebook.com/IMSAgencyFanpage?mibextid=ZbWKwL',
      },
      {
        name: 'Instagram',
        url: 'https://www.instagram.com',
      },
      {
        name: 'LinkedIn',
        url: 'https://vn.linkedin.com/in/imsvn',
      },
    ],
  },
];

type TColumn = {
  header: string;
  items: TRow[];
};

type TRow = {
  name: string;
  url?: string;
  type?: 'email' | 'tel' | 'address' | 'url' | string;
};

const Column = ({ header, items, ...props }: TColumn) => {
  const handleClick = (event: MouseEvent<HTMLAnchorElement>, item: TRow) => {
    event.preventDefault();

    if (item.type === 'email') {
      window.location.href = `mailto:${item.name}`;
    } else if (item.type === 'tel') {
      window.location.href = `tel:${item.name}`;
    } else if (item.type === 'address') {
      // Replace with the actual encoded address for Google Maps
      const encodedAddress = encodeURIComponent(item.name);
      window.open(
        `https://www.google.com/maps/search/?api=1&query=${encodedAddress}`,
      );
    } else if (item.url) {
      window.location.href = item.url;
    }
  };
  return (
    <div className="basis-full font-bold" {...props}>
      <h5 className="mb-3">{header}</h5>
      <ul>
        {items.map((item: TRow) => (
          <li key={item.name} className="mb-1 text-base font-normal">
            <a
              href={item.type === 'url' ? item.url : '#'}
              onClick={(e) => handleClick(e, item)}
            >
              {item.name}
            </a>
          </li>
        ))}
      </ul>
    </div>
  );
};

const GetInTouch = () => {
  const handleClick = (event: MouseEvent<HTMLDivElement>) => {
    const email = 'inquiry@ims.vn';
    event.preventDefault();
    window.location.href = `mailto: ${email}`;
  }
  return (
    <div onClick={handleClick} className="-mb-3 flex max-w-none items-center gap-5 leading-tight cursor-pointer">
      <p className="ml-5 text-[80px] font-extrabold">Get in touch</p>
      <span className="text-primary">
        <ArrowTopRight />
      </span>
    </div>
  );
};

const Footer = () => {
  return (
    <footer id="footer" className="-mt-1 overflow-hidden bg-black text-white">
      <div className="mx-auto flex max-w-screen-2xl flex-col justify-between sm:h-[464px]">
        <div className="mb-10 flex flex-wrap gap-[min(5vw,2.5rem)] overflow-hidden px-[min(3vw,2.5rem)] pt-20 sm:flex-nowrap">
          <h3 className="mb-3 basis-full text-5xl font-bold">
            Let's start creating <br />
            together
          </h3>
          <div className="flex basis-full gap-[min(5vw,2.25rem)] sm:flex-nowrap">
            {columns.map((item: TColumn) => (
              <Column
                key={item.header}
                header={item.header}
                items={item.items}
              />
            ))}
          </div>
        </div>
        <div className="flex">
          <Marquee velocity={2}>
            <GetInTouch />
            <GetInTouch />
            <GetInTouch />
            <GetInTouch />
            <GetInTouch />
            <GetInTouch />
          </Marquee>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
